import {useMutation, useQuery, useQueryClient} from 'react-query';
import {useHistory} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {Story} from '../../api/story.types';
import {getHandbookStoryRequest, handbookViewedRequest} from '../../api/Story.api';
import {useGameStore} from '../Game.store';

interface HandbookStore {
  readonly isHandbookFetching: boolean;
  readonly handbookStory: Story | null;
  readonly markHandbookViewed: () => void;
}

export const useHandbookStore = (): HandbookStore => {
  const queryClient = useQueryClient();
  const {gameState, isGameStoreFetching} = useGameStore();
  const history = useHistory();
  const {
    data,
    isFetching,
  } = useQuery<Story>('handbook-story', getHandbookStoryRequest, {
    staleTime: Infinity,
  });
  const markHandbookViewedMutation = useMutation(handbookViewedRequest, {
    onSuccess: () => {
      queryClient.invalidateQueries('game');
      history.push(Routes.Game);
    },
  });

  const markHandbookViewed = (): void => {
    if (gameState.isHandbookViewed) {
      history.push(Routes.Home);
    } else {
      markHandbookViewedMutation.mutate();
    }
  };

  return {
    isHandbookFetching: isFetching || isGameStoreFetching || markHandbookViewedMutation.isLoading,
    handbookStory: data || null,
    markHandbookViewed,
  };
};
