import React, {FC} from 'react';
import {useHistory} from 'react-router-dom';
import {StoryScreen} from '../common/StoryScreen.component';
import {Routes} from '../../app/routes';
import {useHandbookStore} from './Handbook.store';
import {useGameStore} from '../Game.store';

export const Handbook: FC = () => {
  const history = useHistory();
  const {gameState, isGameStoreFetching} = useGameStore();
  const {handbookStory, isHandbookFetching, markHandbookViewed} = useHandbookStore();

  const handleStart = () => {
    markHandbookViewed();
  };

  return (
    <StoryScreen
      pages={!handbookStory ? [] : handbookStory.pages}
      isFetching={isHandbookFetching || isGameStoreFetching}
      onStart={handleStart}
      onClose={gameState.isHandbookViewed ? () => history.push(Routes.Home) : undefined}
      lastItemButtonTitle="Continue"
    />
  );
};
