import React, {FC, useState} from 'react';
import {useHistory, useLocation} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {ReactComponent as CloseIcon} from '../../images/close.svg';
import {Spinner} from '../../common/Spinner.component';
import styles from './ArTask.module.scss';

export const ArTask: FC = () => {
  const history = useHistory();
  const location = useLocation<{
    readonly videoUrl: string;
    readonly currentTaskIndex: number;
  }>();
  const [isArIframeLoaded, setIsArIframeLoaded] = useState(false);
  const videoId = location.state.videoUrl
    ? location.state.videoUrl.split('/').pop()
    : '';
  const videoSrc = `https://player.vimeo.com/video/${videoId}`;

  return (
    <div className={styles.arTaskContainer}>
      <Spinner isLoading={!isArIframeLoaded && (!!videoId && videoId.length > 0)}/>
      <div className={styles.arTaskFrameContainer}>
        <button className={styles.closeButton} onClick={() => {
          history.push({
            pathname: Routes.Mission,
            state: {
              taskIndex: location.state.currentTaskIndex,
            }
          });
        }}
        >
          <CloseIcon className={styles.closeButtonIcon}/>
        </button>
        {videoId && videoId.length > 0
          ? (
            <iframe
              className={styles.arTaskFrame}
              title="vr-task"
              src={`${process.env.PUBLIC_URL}/ar_task/video.html?videoSrc=${videoSrc}`}
              onLoad={() => {
                setIsArIframeLoaded(true);
              }}
            />)
          : (
            <div className={styles.errorMessage}>
              Error: Can not get video link
            </div>
          )}
      </div>
    </div>
  );
};
