import React, {FC} from 'react';
import LogoImg from '../images/logo512.png';
import styles from './Install.module.scss';

export const Install: FC = () => {
  return (
    <div className={styles.install}>
      <div className={styles.installContent}>
        <div className={styles.icon}>
          <img className={styles.iconImg} src={LogoImg} alt=""/>
        </div>
        <h1 className={styles.title}>Youre just two taps away.</h1>
        <div>
          <p>For the best experience add the app to your home screen.</p>
          <h3>For iOS users:</h3>
          <ol>
            <li>Open this website in Safari.</li>
            <li>Tap the Share button.</li>
            <li>Scroll down and tap Add to Home Screen.</li>
            <li>Tap add button in the top right corner.</li>
            <li>Done, the app is on your home screen!</li>
          </ol>
          <h3>For Android users:</h3>
          <ol>
            <li>Open this website in Chrome.</li>
            <li>Tap menu (3 dots)</li>
            <li>Tap Install app or Add to Home Screen</li>
            <li>Done, the app is on your home screen!</li>
            <br/>
            <p>
              (Alternatively, wait for a few seconds, tap Add app to Home screen on the bottom of your screen and then choose Install)
            </p>
            <p>
              If there is no option to install the app on Android please try one or both from the following:
              <ul>
                <li>clean up some space on the device</li>
                <li>clear cache in Chrome browser</li>
              </ul>
            </p>
            <p>
              If nothing helps please contact our Help Agent - <a className={styles.link} target="_blank" rel="noreferrer" href="https://t.me/helpforce1">https://t.me/helpforce1</a>.
            </p>
          </ol>
        </div>
      </div>
    </div>
  );
};
